/* layout */
#page {
  display: flex;
  min-height: 100vh;
  flex-direction: column; }

#content {
  flex: 1 0 auto; }

#main-content {
  outline: none; }

.container {
  width: auto;
  margin-left: auto;
  margin-right: auto;
  padding: 0 16px; }

.full-width .container {
  width: auto !important; }

.layout-left .container {
  margin-left: 0; }

.sidebar {
  width: auto; }

#main .block {
  margin-top: 24px;
  margin-bottom: 24px; }

#navbar {
  height: 64px;
  padding: 8px 4px;
  position: relative;
  z-index: 30;
  color: #FFF;
  text-align: center; }

#header {
  background: #607d8b;
  color: #FFF;
  padding-top: 64px;
  margin-top: -64px;
  position: relative;
  z-index: 10; }

#header-bg {
  display: none; }

.navbar-fixed #navbar {
  position: fixed;
  top: 0;
  width: 100%;
  background: #607d8b;
  z-index: 30; }

.navbar-fixed #header {
  padding-top: 64px;
  position: relative;
  z-index: 10;
  margin-top: 0; }

.navbar-transparent #navbar {
  background: none;
  pointer-events: none; }
  .navbar-transparent #navbar .navbar-item {
    pointer-events: auto; }

.navbar-transparent #navbar.navbar-bg {
  background: none; }

.navbar-light #navbar {
  color: rgba(0, 0, 0, 0.87); }

.navbar-light #header {
  background: #FFFFFF; }

.navbar-light.navbar-fixed #navbar {
  background: #FFFFFF; }

.navbar-light .navbar-item .navbar-icon, .navbar-light .navbar-slogan, .navbar-light .navbar-title a {
  color: rgba(0, 0, 0, 0.87); }

.navbar-light .navbar-menu .menu a {
  color: rgba(0, 0, 0, 0.87); }

.navbar-light .navbar-menu .menu a:hover {
  color: rgba(0, 0, 0, 0.87);
  border-color: rgba(0, 0, 0, 0.5); }

.navbar-light .navbar-menu .menu .active-trail a, .navbar-light .navbar-menu .menu .is-active {
  border-color: rgba(0, 0, 0, 0.87); }

.navbar-dark #navbar {
  color: #FFF; }

.navbar-dark #header {
  background: #212121; }

.navbar-dark.navbar-fixed #navbar {
  background: #212121; }

.navbar-dark .navbar-item .navbar-icon, .navbar-dark .navbar-slogan, .navbar-dark .navbar-title a {
  color: #FFF; }

.navbar-dark .navbar-menu .menu a {
  color: #FFF; }

.navbar-dark .navbar-menu .menu a:hover {
  color: #FFF;
  border-color: rgba(255, 255, 255, 0.5); }

.navbar-dark .navbar-menu .menu .active-trail a, .navbar-dark .navbar-menu .menu .is-active {
  border-color: #FFF; }

#footer {
  min-height: 48px;
  background: #f5f5f5;
  font-size: 14px; }

#footer-content {
  padding: 8px 4px; }

.region-footer-content {
  display: flex;
  flex-wrap: wrap; }

.footer-item {
  display: inline-block;
  min-height: 32px;
  margin: 0 12px;
  float: none;
  line-height: 32px; }

.footer-icons {
  display: flex;
  align-items: center;
  min-height: 32px; }

.footer-transparent #footer {
  background: none;
  pointer-events: none; }
  .footer-transparent #footer .footer-item {
    pointer-events: auto; }

@media (min-width: 568px) {
  #navbar {
    padding: 8px 12px; }
  #footer {
    min-height: 64px; }
  #footer-content {
    padding: 8px 12px;
    text-align: center; }
  .region-footer-content {
    display: block; }
  .footer-item {
    min-height: 48px;
    float: left;
    line-height: 48px; }
  .footer-item.pull-center {
    float: none; }
  .footer-item.pull-right {
    float: right; }
  .footer-icons {
    min-height: 48px; } }

